						console.log('当前位置的纬度：' + res.longitude);
						that.currPoint[0] = res.longitude
						that.currPoint[1] = res.latitude
						// console.log(that.currPoint)
						// console.log('that   '+that)
						uni.$emit('getPoint', that.currPoint)
						// 提示信息
						uni.showToast({
						                      title: "定位成功",
						                      icon: 'success',
						                      duration:1000,
						                      mask: true
						                    })
					}
				});

			},
